import React from 'react';
import './mainCounter.css';

const MainCounter = ({mainCounter, numberWithCommas}) => {
    let displayBalance = numberWithCommas(mainCounter);
    return(
        <div className='washed-green dib br3 ma1 bw2 shadow-5 ph2 pv1'>
        Bank Account:
        <div className='green f2'>${displayBalance}</div>

        </div>
    );
}
export default MainCounter;