import React from 'react';
import './card.css';

const Card = ({upgradeId, name, cost, effect, owned, happinessCost, numberWithCommas}) => {
    let displayCost = numberWithCommas(cost);
    let displayHappy = numberWithCommas(happinessCost);
    if (owned === false){
        return(
            <div  id = {'upgrade' + upgradeId} className='tc bg-light-green dib br3 ma2 grow bw2 shadow-5'>
                <img alt='roboimg' src={`https://robohash.org/${upgradeId}?set=set4`}/>
                <div>
                    <h2 className='ph2'>{name}</h2>
                    <p className='ph2'>{effect}</p>
                    <h5>Price</h5>
                    <h2 className='ph2'>&#x1f4b0;${displayCost} &#x1F604; {displayHappy}</h2>
                </div>
            </div>
        );
    } else {
        return(
            <div  id = {'upgrade' + upgradeId} className='tc bg-light-yellow dib br3 ma2 grow bw2 shadow-5'>
                <img alt='roboimg' src={`https://robohash.org/${upgradeId}?set=set4`}/>
                <div>
                    <h2 className='ph2'>{name}</h2>
                    <p className='ph2'>{effect}</p>
                    <h2 className='ph2'>OWNED</h2>
                    
                </div>
            </div>
        );

    };


}
export default Card;